.feedPageRightSideBarContainer {
  /* position: fixed; */
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.feedPageRightSideBarRequestsContainer {
  background: white;
  height: 18rem;
  min-width: 15rem;
  padding: 1rem 2rem;
  border-radius: var(--border-radius);
  overflow-y: scroll;
}

.feedPageRightSideBarRequestsContainer h4 {
  font-weight: 500;
  margin-bottom: 1rem;
}

.feedPageRightSideBarFriendsContainer {
  background: white;
  min-width: 15rem;
  padding: 1rem 2rem;
  border-radius: var(--border-radius);
  height: 19rem;
  overflow-y: scroll;

}

.feedPageRightSideBarFriendsContainer h4 {
  font-weight: 500;
  margin-bottom: 1rem;
}

@media (max-width: 1201px) {
  .feedPageRightSideBarContainer {
    display: none;
  }
}